import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ButtonBase from '@material-ui/core/ButtonBase'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import Divider from '@material-ui/core/Divider'
import EditOutlinedIcon from '@material-ui/icons/EditOutlined'
import PostAddOutlinedIcon from '@material-ui/icons/PostAddOutlined'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import DeleteSweepOutlinedIcon from '@material-ui/icons/DeleteSweepOutlined'
import DeleteOutlineOutlinedIcon from '@material-ui/icons/DeleteOutlineOutlined'
import color from 'tinycolor2'

function MoreSelector({ row, handleChange }) {
  const classes = useStyles()
  const [menuAnchor, setMenuAnchor] = useState(null)

  const handleMenuClick = (change) => {
    setMenuAnchor(null)
    if (change) handleChange(change)
  }

  return (
    <div className={classes.root}>
      <ButtonBase
        className={classes.rowItem}
        aria-controls="task-menu"
        aria-haspopup="true"
        onClick={(event) => setMenuAnchor(event.currentTarget)}
      >
        <MoreVertIcon color="action" />
      </ButtonBase>
      <Menu
        id="task-menu"
        anchorEl={menuAnchor}
        keepMounted
        open={Boolean(menuAnchor)}
        onClose={() => setMenuAnchor(null)}
      >
        <MenuItem onClick={() => handleMenuClick({ title: row?.title, action: 'editPortfolio' })}>
          <ListItemIcon>
            <EditOutlinedIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText primary="Edit Portfolio" />
        </MenuItem>
        <Divider />

        {row?.bool === '1' && (
          <MenuItem onClick={() => handleMenuClick({ title: row?.title, action: 'addPortfolio' })}>
            <ListItemIcon>
              <PostAddOutlinedIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText primary="Add a Nested Portfolio" />
          </MenuItem>
        )}
        {row?.bool === '1' && <Divider />}
        {row?.bool === '1' && (
          <MenuItem onClick={() => handleMenuClick({ title: row?.title, action: 'deletetree' })}>
            <ListItemIcon>
              <DeleteSweepOutlinedIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText primary="Delete Portfolio Tree" />
          </MenuItem>
        )}
        {row?.bool === '1' && <Divider />}
        <MenuItem onClick={() => handleMenuClick({ title: row?.title, action: 'deletenode' })}>
          <ListItemIcon>
            <DeleteOutlineOutlinedIcon fontSize="small" />
          </ListItemIcon>
          <ListItemText primary="Delete Portfolio" />
        </MenuItem>
      </Menu>
    </div>
  )
}

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100%',
    borderLeft: `1px solid ${color(theme.palette.divider).setAlpha(0.06).toString()}`,
  },
  rowItem: {
    height: '100%',
    width: 48,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    overflow: 'hidden',
    zIndex: 1,
    '&:hover': {
      '&:after': {
        position: 'absolute',
        top: 0,
        bottom: 0,
        left: 0,
        right: 0,
        content: '""',
        backgroundColor: color(theme.palette.divider).setAlpha(0.03).toString(),
      },
    },
  },
}))

MoreSelector.propTypes = {
  row: PropTypes.object,
  handleChange: PropTypes.func,
}

MoreSelector.defaultProps = {
  row: null,
  handleChange: () => {},
}

export default React.memo(MoreSelector)
