/* eslint-disable no-unused-vars */
import React, { Children, cloneElement } from 'react'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'
import classnames from 'classnames'

function TreeNode({
  children,
  listIndex,
  swapFrom,
  swapLength,
  swapDepth,
  scaffoldBlockPxWidth,
  lowerSiblingCounts,
  connectDropTarget,
  isOver,
  draggedNode,
  canDrop,
  treeIndex,
  treeId, // Delete from otherProps
  getPrevRow, // Delete from otherProps
  node, // Delete from otherProps
  path, // Delete from otherProps
  rowDirection, // Not needed (for my purposes)
  ...otherProps
}) {
  const classes = useStyles()

  // Construct the scaffold representing the structure of the tree
  const scaffoldBlockCount = lowerSiblingCounts.length
  const scaffold = []
  lowerSiblingCounts.forEach((lowerSiblingCount, i) => {
    let lineClasses = []
    if (lowerSiblingCount > 0) {
      // At this level in the tree, the nodes had sibling nodes further down
      if (listIndex === 0) {
        // Top-left corner of the tree
        lineClasses = [classes.lineHalfHorizontalRight, classes.lineHalfVerticalBottom]
      } else if (i === scaffoldBlockCount - 1) {
        // Last scaffold block in the row, right before the row content
        lineClasses = [classes.lineHalfHorizontalRight, classes.lineFullVertical]
      } else {
        // Simply connecting the line extending down to the next sibling on this level
        lineClasses = [classes.lineFullVertical]
      }
    } else if (listIndex === 0) {
      // Top-left corner of the tree, but has no siblings
      lineClasses = [classes.lineHalfHorizontalRight]
    } else if (i === scaffoldBlockCount - 1) {
      // The last or only node in this level of the tree
      lineClasses = [classes.lineHalfVerticalTop, classes.lineHalfHorizontalRight]
    }

    scaffold.push(
      <div
        key={`pre_${1 + i}`}
        style={{ width: scaffoldBlockPxWidth }}
        className={classnames(classes.lineBlock, lineClasses)}
      />
    )

    if (treeIndex !== listIndex && i === swapDepth) {
      // This row has been shifted, and is at the depth of
      // the line pointing to the new destination
      let highlightLineClass = []

      if (listIndex === swapFrom + swapLength - 1) {
        // This block is on the bottom (target) line
        // This block points at the target block (where the row will go when released)
        highlightLineClass = classes.highlightBottomLeftCorner
      } else if (treeIndex === swapFrom) {
        // This block is on the top (source) line
        highlightLineClass = classes.highlightTopLeftCorner
      } else {
        // This block is between the bottom and top
        highlightLineClass = classes.highlightLineVertical
      }

      scaffold.push(
        <div
          // eslint-disable-next-line react/no-array-index-key
          key={i}
          style={{
            width: scaffoldBlockPxWidth,
            left: scaffoldBlockPxWidth * i,
          }}
          className={classnames(classes.absoluteLineBlock, highlightLineClass)}
        />
      )
    }
  })

  return connectDropTarget(
    <div {...otherProps} className={classnames(classes.node)}>
      {scaffold}
      <div className={classes.nodeContent} style={{ left: scaffoldBlockPxWidth * scaffoldBlockCount }}>
        {Children.map(children, (child) => cloneElement(child, { isOver, canDrop, draggedNode }))}
      </div>
    </div>
  )
}

const useStyles = makeStyles((theme) => ({
  node: {
    minWidth: '100%',
    whiteSpace: 'nowrap',
    position: 'relative',
    textAlign: 'left',
  },
  nodeContent: {
    position: 'absolute',
    top: 0,
    bottom: 0,
    right: 0,
  },
  // Scaffold
  // Line-overlaid blocks used for showing the tree structure
  lineBlock: {
    height: '100%',
    display: 'inline-block',
    position: 'relative',
  },
  absoluteLineBlock: {
    height: '100%',
    display: 'inline-block',
    position: 'absolute',
    top: 0,
  },
  lineHalfHorizontalRight: {
    '&:before': {
      position: 'absolute',
      content: '""',
      backgroundColor: theme.palette.divider,
      height: 1,
      top: '50%',
      right: 0,
      width: '50%',
    },
  },
  lineFullVertical: {
    '&:after': {
      position: 'absolute',
      content: '""',
      backgroundColor: theme.palette.divider,
      width: 1,
      left: '50%',
      top: 0,
      height: '100%',
    },
  },
  lineHalfVerticalTop: {
    '&:after': {
      position: 'absolute',
      content: '""',
      width: 1,
      left: '50%',
      top: 0,
      height: '50%',
      backgroundColor: theme.palette.divider,
    },
  },
  lineHalfVerticalBottom: {
    '&:after': {
      position: 'absolute',
      content: '""',
      backgroundColor: theme.palette.divider,
      width: 1,
      left: '50%',
      top: 'auto',
      bottom: 0,
      height: '50%',
    },
  },
  // Highlight line for pointing to dragged row destination
  '@keyframes arrow-pulse': {
    '0%': {
      transform: 'translate(0, 0)',
      opacity: 0,
    },
    '30%': {
      transform: 'translate(0, 300%)',
      opacity: 1,
    },
    '70%': {
      transform: 'translate(0, 700%)',
      opacity: 1,
    },
    '100%': {
      transform: 'translate(0, 1000%)',
      opacity: 0,
    },
  },
  highlightLineVertical: {
    zIndex: 3,
    '&:before': {
      position: 'absolute',
      content: '""',
      backgroundColor: theme.palette.primary.light,
      width: 8,
      marginLeft: -4,
      left: '50%',
      top: 0,
      height: '100%',
    },
    '&:after': {
      content: '""',
      position: 'absolute',
      height: 0,
      marginLeft: -4,
      left: '50%',
      top: 0,
      borderLeft: `4px solid transparent`,
      borderRight: `4px solid transparent`,
      borderTop: `4px solid white`,
      animation: `$arrow-pulse 1s infinite linear both`,
    },
  },
  highlightTopLeftCorner: {
    zIndex: 3,
    '&:before': {
      content: '""',
      position: 'absolute',
      borderTop: `8px solid ${theme.palette.primary.light}`,
      borderLeft: `8px solid ${theme.palette.primary.light}`,
      boxSizing: 'border-box',
      height: `calc(50% + 4px)`,
      top: `50%`,
      marginTop: -4,
      right: 0,
      width: `calc(50% + 4px)`,
    },
  },
  highlightBottomLeftCorner: {
    zIndex: 3,
    '&:before': {
      content: '""',
      position: 'absolute',
      borderBottom: `8px solid ${theme.palette.primary.light}`,
      borderLeft: `8px solid ${theme.palette.primary.light}`,
      boxSizing: 'border-box',
      height: `calc(100% + 4px)`,
      top: 0,
      right: 12,
      width: `calc(50% - 8px)`,
    },
    '&:after': {
      content: '""',
      position: 'absolute',
      height: 0,
      right: 0,
      top: '100%',
      marginTop: -12,
      borderTop: '12px solid transparent',
      borderBottom: '12px solid transparent',
      borderLeft: `12px solid ${theme.palette.primary.light}`,
    },
  },
}))

TreeNode.propTypes = {
  treeIndex: PropTypes.number.isRequired,
  treeId: PropTypes.string.isRequired,
  swapFrom: PropTypes.number,
  swapDepth: PropTypes.number,
  swapLength: PropTypes.number,
  scaffoldBlockPxWidth: PropTypes.number.isRequired,
  lowerSiblingCounts: PropTypes.arrayOf(PropTypes.number).isRequired,

  listIndex: PropTypes.number.isRequired,
  children: PropTypes.node.isRequired,

  // Drop target
  connectDropTarget: PropTypes.func.isRequired,
  isOver: PropTypes.bool.isRequired,
  canDrop: PropTypes.bool,
  draggedNode: PropTypes.shape({}),

  // used in dndManager
  getPrevRow: PropTypes.func.isRequired,
  node: PropTypes.shape({}).isRequired,
  path: PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.number])).isRequired,

  // rtl support
  rowDirection: PropTypes.string,
}

TreeNode.defaultProps = {
  swapFrom: null,
  swapDepth: null,
  swapLength: null,
  canDrop: false,
  draggedNode: null,
  rowDirection: 'ltr',
}

export default TreeNode
