/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React from 'react'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'
import classnames from 'classnames'
import DragIndicatorIcon from '@material-ui/icons/DragIndicator'
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight'
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CircularProgress from '@material-ui/core/CircularProgress'
import color from 'tinycolor2'

function isDescendant(older, younger) {
  return (
    !!older.children &&
    typeof older.children !== 'function' &&
    older.children.some((child) => child === younger || isDescendant(child, younger))
  )
}

function NodeRendererDefault(props) {
  const {
    scaffoldBlockPxWidth,
    toggleChildrenVisibility,
    connectDragPreview,
    connectDragSource,
    isDragging,
    canDrop,
    canDrag,
    node,
    title,
    subtitle, // Not needed (for my purposes)
    nationality,
    dateranges,
    draggedNode,
    path,
    treeIndex,
    isSearchMatch,
    isSearchFocus,
    buttons,
    className,
    style,
    didDrop,
    treeId,
    isOver, // Not needed, but preserved for other renderers
    parentNode, // Needed for dndManager
    rowDirection, // Not needed (for my purposes)
    ...otherProps
  } = props
  const classes = useStyles()
  const nodeTitle = title || node.title
  const nodeSubtitle = subtitle || node.subtitle
  const nodeNationality = nationality || node.nationality
  const nodeDateranges = dateranges || node.dateranges

  let handle
  if (canDrag) {
    if (typeof node.children === 'function' && node.expanded) {
      // Show a loading symbol on the handle when the children are expanded
      // and yet still defined by a function (a callback to fetch the children)
      handle = (
        /*
        <div className={classes.loadingHandle}>
          <CircularProgress size={24} />
        </div>
        */
        <div className={classes.loadingHandle}>
          <div className={classes.loadingCircle}>
            {[...new Array(12)].map((_, index) => (
              <div
                // eslint-disable-next-line react/no-array-index-key
                key={index}
                className={classes.loadingCirclePoint}
              />
            ))}
          </div>
        </div>
      )
    } else {
      // Show the handle used to initiate a drag-and-drop
      handle = connectDragSource(
        <div className={classes.moveHandle}>
          <DragIndicatorIcon color="action" />
        </div>,
        { dropEffect: 'copy' }
      )
    }
  }

  const isDraggedDescendant = draggedNode && isDescendant(draggedNode, node)
  const isLandingPadActive = !didDrop && isDragging
  const hasChildren = node?.children?.length > 0 || typeof node?.children === 'function'

  const nodeContent = connectDragPreview(
    <div
      className={classnames(
        classes.rowContent,
        isLandingPadActive && classes.rowLandingPad,
        isLandingPadActive && !canDrop && classes.rowCancelPad,
        isSearchMatch && classes.rowSearchMatch,
        isSearchFocus && classes.rowSearchFocus,
        node?.data?.deleted && classes.rowContentDeleted,
        className
      )}
      style={{ opacity: isDraggedDescendant ? 0.5 : 1, ...style }}
    >
      <div className={classnames(classes.rowLabel)}>
        {typeof nodeTitle === 'function' ? nodeTitle({ node, path, treeIndex }) : <Typography>{nodeTitle}</Typography>}
      </div>
      <div style={{ height: '100%' }}></div>
      <div className={classnames(classes.rowLabelSubtitle)}>
        {typeof nodeSubtitle === 'function' ? (
          nodeSubtitle({ node, path, treeIndex })
        ) : (
          <Typography>
            <span style={{ fontSize: '70%', color: 'grey' }}>Legal Entity</span>
            <br />
            {nodeSubtitle}
          </Typography>
        )}
      </div>
      <div className={classnames(classes.rowLabelSubtitle)}>
        {typeof nodeDateranges === 'function' ? (
          nodeDateranges({ node, path, treeIndex })
        ) : (
          <Typography>
            <span style={{ fontSize: '70%', color: 'grey' }}>Date Range</span>
            <br />
            {nodeDateranges[0].map((value, index) => {
              if (index > 0) {
                value = ' - ' + value
              }
              return <span key={index}>{value}</span>
            })}
          </Typography>
        )}
      </div>
      <div className={classnames(classes.rowLabelContent)}>
        {typeof nodeNationality === 'function' ? (
          nodeNationality({ node, path, treeIndex })
        ) : (
          <Typography>
            <span style={{ fontSize: '70%', color: 'grey' }}>Nationality</span>
            <br />
            {nodeNationality}
          </Typography>
        )}
      </div>
      <div className={classes.rowToolbar}>{buttons}</div>
    </div>
  )

  return (
    <div
      className={classes.rowContainer}
      style={{
        opacity: isDraggedDescendant ? 0.5 : 1,
        ...style,
      }}
      {...otherProps}
    >
      {toggleChildrenVisibility && hasChildren && (
        <div className={classes.toggleIcon}>
          <IconButton
            size="small"
            onClick={(event) => {
              event.target.blur()
              toggleChildrenVisibility({ node, path, treeIndex })
            }}
          >
            {node.expanded ? <KeyboardArrowDownIcon color="action" /> : <KeyboardArrowRightIcon color="action" />}
          </IconButton>
        </div>
      )}

      <div className={classes.rowWrapper + (!canDrag ? ` ${classes.rowWrapperDragDisabled}` : '')}>
        {canDrag ? connectDragSource(nodeContent, { dropEffect: 'copy' }) : nodeContent}
      </div>
      <div className={classes.lineLeft} />
      {toggleChildrenVisibility && hasChildren && node.expanded && !isDragging && (
        <div className={classes.lineChildren} />
      )}
    </div>
  )
}

const useStyles = makeStyles((theme) => ({
  rowContainer: {
    flex: '1 1 auto',
    whiteSpace: 'nowrap',
    boxSizing: 'border-box',
    '& * ': {
      boxSizing: 'border-box',
    },
    height: '100%',
    padding: theme.spacing(0.5),
  },
  rowWrapper: {
    height: '100%',
    boxSizing: 'border-box',
  },
  rowContent: {
    height: '100%',
    display: 'flex',
    border: `1px solid ${theme.palette.divider}`,
    backgroundColor: theme.palette.background.paper,
    borderRadius: '10px', //theme.shape.borderRadius,
    overflow: 'hidden',
    '&:hover': {
      border: `1px solid ${color(theme.palette.divider).setAlpha(0.3).toString()}`,
    },
  },
  rowContentDeleted: {
    position: 'relative',
    opacity: 0.5,
    '&:after': {
      position: 'absolute',
      content: '""',
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
      backgroundColor: color(theme.palette.background.paper).setAlpha(0.6).toString(),
      border: `1px solid ${color(theme.palette.background.paper).setAlpha(0.6).toString()}`,
      pointerEvents: 'none',
    },
  },
  // The outline of where the element will go if dropped, displayed while dragging
  rowLandingPad: {
    border: 'none',
    boxShadow: 'none',
    backgroundColor: 'transparent',
    outline: 'none',
    '& > *': {
      opacity: 0,
    },
    '&:hover': {
      border: 'none',
    },
    '&:before': {
      backgroundColor: theme.palette.primary.light,
      border: `2px dashed ${theme.palette.primary.dark}`,
      borderRadius: theme.shape.borderRadius,
      content: '""',
      position: 'absolute',
      top: theme.spacing(0.5),
      right: theme.spacing(0.5),
      bottom: theme.spacing(0.5),
      left: theme.spacing(0.5),
      zIndex: -1,
      opacity: 0.25,
    },
  },
  rowCancelPad: {
    border: 'none',
    boxShadow: 'none',
    backgroundColor: 'transparent',
    outline: 'none',
    '& > *': {
      opacity: 0,
    },
    '&:before': {
      backgroundColor: theme.palette.secondary.light,
      border: `2px dashed ${theme.palette.secondary.dark}`,
      borderRadius: theme.shape.borderRadius,
      content: '""',
      position: 'absolute',
      top: theme.spacing(0.5),
      right: theme.spacing(0.5),
      bottom: theme.spacing(0.5),
      left: theme.spacing(0.5),
      zIndex: -1,
      opacity: 0.25,
    },
  },
  // Nodes matching the search conditions are highlighted
  rowSearchMatch: {
    border: `2px solid ${theme.palette.primary.main}`,
    //backgroundColor: theme.palette.primary.light,
  },
  // The node that matches the search conditions and is currently focused
  rowSearchFocus: {
    border: `2px dashed ${theme.palette.primary.main}`,
  },
  rowLabel: {
    display: 'flex',
    alignItems: 'center',
    flex: '0 1 auto',
    //paddingRight: theme.spacing(1),
    overflow: 'hidden',
    height: '100%',
    //top: '00%',
    //left: '50%',
    //transform: 'translate(-50%,-50%)',
    marginLeft: '60px',
    paddingRight: '20px',
    borderRight: `1px solid ${color(theme.palette.divider).setAlpha(0.06).toString()}`,
  },
  rowLabelSubtitle: {
    display: 'flex',
    alignItems: 'center',
    flex: '0 1 auto',
    paddingRight: theme.spacing(1),
    overflow: 'hidden',
    marginLeft: '20px',
    //borderRight: `1px solid ${color(theme.palette.divider).setAlpha(0.06).toString()}`,
  },
  rowLabelContent: {
    display: 'flex',
    alignItems: 'center',
    flex: '1 1 auto',
    paddingRight: theme.spacing(1),
    overflow: 'hidden',
    marginLeft: '40px',
    //borderRight: `1px solid ${color(theme.palette.divider).setAlpha(0.06).toString()}`,
  },
  rowToolbar: {
    display: 'flex',
    alignItems: 'center',
    flex: '0 1 auto',
  },
  isNotDraggable: {
    height: '100%',
    width: theme.spacing(2),
  },
  moveHandle: {
    height: '100%',
    cursor: 'move',
    zIndex: 1,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    marginLeft: '30px',
    width: 44,
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
  },
  loadingHandle: {
    height: '100%',
    zIndex: 1,
    cursor: 'default',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
  },
  //addons
  loadingCircle: {
    width: '80%',
    height: '80%',
    margin: '10%',
    position: 'relative',
  },
  loadingCirclePoint: {
    width: '100%',
    height: '100%',
    position: 'absolute',
    left: 0,
    top: 0,
    '&:before': {
      content: '""',
      display: 'block',
      margin: '0 auto',
      width: '11%',
      height: '30%',
      backgroundColor: '#fff',
      borderRadius: '30%',
      animation: 'pointFade 800ms infinite ease-in-out both',
    },
    '&:nth-of-type(1)': {
      transform: 'rotate(0deg)',
      '&:before': {
        animationDelay: '-800ms',
      },
    },
    '&:nth-of-type(7)': {
      transform: 'rotate(0deg)',
      '&:before': {
        animationDelay: '-800ms',
      },
    },
    '&:nth-of-type(2)': {
      transform: 'rotate(30deg)',
      '&:before': {
        animationDelay: '-666ms',
      },
    },
    '&:nth-of-type(8)': {
      transform: 'rotate(210deg)',
      '&:before': {
        animationDelay: '-666ms',
      },
    },
    '&:nth-of-type(3)': {
      transform: 'rotate(60deg)',
      '&:before': {
        animationDelay: '-533ms',
      },
    },
    '&:nth-of-type(9)': {
      transform: 'rotate(240deg)',
      '&:before': {
        animationDelay: '-533ms',
      },
    },
    '&:nth-of-type(4)': {
      transform: 'rotate(90deg)',
      '&:before': {
        animationDelay: '-400ms',
      },
    },
    '&:nth-of-type(10)': {
      transform: 'rotate(270deg)',
      '&:before': {
        animationDelay: '-400ms',
      },
    },
    '&:nth-of-type(5)': {
      transform: 'rotate(120deg)',
      '&:before': {
        animationDelay: '-266ms',
      },
    },
    '&:nth-of-type(11)': {
      transform: 'rotate(300deg)',
      '&:before': {
        animationDelay: '-266ms',
      },
    },
    '&:nth-of-type(6)': {
      transform: 'rotate(150deg)',
      '&:before': {
        animationDelay: '-266ms',
      },
    },
    '&:nth-of-type(12)': {
      transform: 'rotate(330deg)',
      '&:before': {
        animationDelay: '-133ms',
      },
    },
    /*'&:nth-of-type(7)': {
    transform: 'rotate(180deg)',
    '&:before': {
      animationDelay: '-266ms'
    }
  },*/
    '&:nth-of-type(13)': {
      transform: 'rotate(360deg)',
      '&:before': {
        animationDelay: '0ms',
      },
    },
  },
  //end addons

  toggleIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    appearance: 'none',
    border: `1px solid ${theme.palette.divider}`, // 'none',
    overflow: 'hidden',
    position: 'absolute',
    borderRadius: '100%',
    //boxShadow: `0 0 0 1px ${theme.palette.divider}`,
    width: 28,
    height: 28,
    padding: 0,
    top: '50%',
    transform: 'translate(-38px, -50%)',
    backgroundColor: theme.palette.background.paper,
    left: '50px',
  },
  lineLeft: {
    height: '100%',
    position: 'absolute',
    left: 0,
    top: 0,
    '&:after': {
      content: '""',
      position: 'absolute',
      backgroundColor: theme.palette.divider,
      width: theme.spacing(0.5),
      top: '50%',
      left: 0,
      height: 1,
    },
  },
  lineChildren: {
    height: '100%',
    position: 'absolute',
    left: 0,
    top: 0,
    '&:after': {
      content: '""',
      position: 'absolute',
      backgroundColor: theme.palette.divider,
      width: 1,
      left: theme.spacing(3) - 2,
      bottom: 0,
      height: theme.spacing(0.5),
    },
  },
}))

NodeRendererDefault.defaultProps = {
  isSearchMatch: false,
  isSearchFocus: false,
  canDrag: false,
  toggleChildrenVisibility: null,
  buttons: [],
  className: '',
  style: {},
  parentNode: null,
  draggedNode: null,
  canDrop: false,
  title: null,
  subtitle: null,
  rowDirection: 'ltr',
}

NodeRendererDefault.propTypes = {
  node: PropTypes.shape({}).isRequired,
  title: PropTypes.oneOfType([PropTypes.func, PropTypes.node]),
  subtitle: PropTypes.oneOfType([PropTypes.func, PropTypes.node]),
  path: PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.number])).isRequired,
  treeIndex: PropTypes.number.isRequired,
  treeId: PropTypes.string.isRequired,
  isSearchMatch: PropTypes.bool,
  isSearchFocus: PropTypes.bool,
  canDrag: PropTypes.bool,
  scaffoldBlockPxWidth: PropTypes.number.isRequired,
  toggleChildrenVisibility: PropTypes.func,
  buttons: PropTypes.arrayOf(PropTypes.node),
  className: PropTypes.string,
  style: PropTypes.shape({}),

  // Drag and drop API functions
  // Drag source
  connectDragPreview: PropTypes.func.isRequired,
  connectDragSource: PropTypes.func.isRequired,
  parentNode: PropTypes.shape({}), // Needed for dndManager
  isDragging: PropTypes.bool.isRequired,
  didDrop: PropTypes.bool.isRequired,
  draggedNode: PropTypes.shape({}),
  // Drop target
  isOver: PropTypes.bool.isRequired,
  canDrop: PropTypes.bool,

  // rtl support
  rowDirection: PropTypes.string,
}

export default NodeRendererDefault
